import React,{useState} from "react"
import Title from "./Title"
import { FaAngleDoubleRight } from "react-icons/fa"
import { graphql, useStaticQuery,usestate } from "gatsby"
import { Link } from "gatsby"

const query = graphql`
{
  allStrapiJobs(sort: {fields: date}) {
    nodes {
      company
      date
      desc {
        id
        name
      }
      position
    }
  }
}
`

const Jobs = () => {
  const data = useStaticQuery(query)
  const {allStrapiJobs:{nodes:jobs},}=data

  const [value, setvalue] = useState(0)

  const {company,position,date,desc}=jobs[value];
  console.log(company,position,date,desc)

  return (
    <section className="section jobs">
      <Title  title='experience'/>
      <div className='jobs-center'>
        <div className='btn-container'>
        {jobs.map((item,index)=>{
          return <button key={item.strapiId} 
          onClick={()=>{setvalue(index)}}
          className={`job-btn ${index===value && "active-btn"}`}>{item.company}</button>
        })}  
        </div>
        <article>
      <h3>{position}</h3>
      <h4>{company}</h4>
      <p className='job-date'>{date}</p>
      {
        desc.map((item)=>{
          return(
           <div key={item.id} className="job-desc">
             <FaAngleDoubleRight className='job-icon'></FaAngleDoubleRight>
             <p>{item.name}</p>
           </div>
          )
        })
      }
        </article>

      </div>
      <Link to ="/about" className='btn center-btn'>More info</Link>
    </section>
  )
}

export default Jobs
